@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;1,200;1,300;1,400;1,600;1,700&display=swap);
/*@import url('https://fonts.googleapis.com/css?family=Roboto:400,500&display=swap');*/

*{
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;

}

html, body, #root {
    height: 100%;
}

body{
    font: 14px 'Nunito', sans-serif;
    background: #ecf1f8;
    color:#333;
    -webkit-font-smoothing:antialiased !important;
}

.tableFlexRow {
    display: flex;
}

.tableFlexColumn {
    flex: 50% 1;
    padding: 4px;
    border: 1px solid #dfdede;
    word-wrap: break-word !important;
    overflow-wrap: break-word !important;
    flex-wrap: wrap;
}
.tableFlexColumn_bg1 {
    background-color:#f1f1f1;
}
.tableFlexColumn_bg1 strong {
    overflow-wrap: break-word;
    word-wrap: break-word;

    -ms-word-break: break-all;
    /* This is the dangerous one in WebKit, as it breaks things wherever */
    word-break: break-all;
    /* Instead use this non-standard one: */
    word-break: break-word;

    /* Adds a hyphen where the word breaks, if supported (No Blink) */
    -ms-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}
.tableFlexColumn_bg2 {
    background-color:#f8f8f8;
}
.tableFlexColumn_bg2 span {
    overflow-wrap: break-word;
    word-wrap: break-word;

    -ms-word-break: break-all;
    /* This is the dangerous one in WebKit, as it breaks things wherever */
    word-break: break-all;
    /* Instead use this non-standard one: */
    word-break: break-word;

    /* Adds a hyphen where the word breaks, if supported (No Blink) */
    -ms-hyphens: auto;
    -webkit-hyphens: auto;
    hyphens: auto;
}

.esri-popup__header-title {
    cursor: move;
 }
/* App brand */
.app-brand {
    position: relative;
    display: block;
    background-color: #2a3850; }
    .app-brand a {
      display: flex;
      flex-direction: row;
      align-items: center;
      height: 75px;
      line-height: 75px;
      width: 400px;
      transition: width 0.3s ease-in-out;
      padding-left: 1.56rem; }
      @media (prefers-reduced-motion: reduce) {
        .app-brand a {
          transition: none; } }
      @media (min-width: 768px) {
        .app-brand a {
          overflow: hidden; } }
      .app-brand a img {
        max-width: 80px;
        vertical-align: middle; }
    .app-brand .brand-name {
      color: #ffffff;
      font-size: 24px;
      font-weight: 400;
      margin-left: 0.94rem;
      max-width: 300px;
      text-decoration: none;
     }


    .card-header {
      background-color: #2a3850;
    }

    .btn-blue-dark {
      background-color: #2a3850;
      color: #ffffff;
    }

    .btn-blue-dark:hover {
      background-color: #1d2839;
      color: #d1d1d1;
    }

    .toastMsg{
      position: absolute;
      top: 10px;
      right: 10px;
      z-index: 1000;
    }

    .form-signin {
      width: 100%;
      max-width: 330px;
      padding: 15px;
      margin: 0 auto;
      background-color: rgba(255, 255, 255, 0.815);
      border-radius: 6px;
      
    }
    .form-signin .checkbox {
      font-weight: 400;
    }
    .form-signin .form-control {
      position: relative;
      box-sizing: border-box;
      height: auto;
      padding: 10px;
      font-size: 16px;
    }
    .form-signin .form-control:focus {
      z-index: 2;
    }
    .form-signin input[type="email"] {
      margin-bottom: -1px;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }
    .form-signin input[type="password"] {
      margin-bottom: 10px;
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
.loading__centered{
    height: 100%;
    width: 100%;
    min-height: 200px;
    background:#c91122;
    overflow: hidden;
    z-index: 9;
    border-radius: 6px;
}
  
.loading__logo {
    display: flex!important;
    position: absolute;
    left: 50%;
    top: 45%;
}


iframe {
    margin: 0px !important;
    padding: 0px !important;
    background: #c91122 !important; /* this is just to make the frames easier to see */
    border: 0px !important;
    overflow: hidden !important;
    display: block !important;
}

